import { Container } from "./styled";
import devilLaugh from "../../../sounds/devil.wav";
import bodyCut from "../../../sounds/bodyCut.wav";
import thunder from "../../../sounds/thunder.wav";
import wolf from "../../../sounds/wolf.wav";
import zombie from "../../../sounds/zombie.wav";
import stomp from "../../../sounds/stomp.wav";
import horror from "../../../sounds/horror.wav";
import drum from "../../../sounds/drum.wav";
import sweep from "../../../sounds/sweep.wav";
import jigsaw from "../../../sounds/jigsaw.wav";

const effects = [
  {
    image: require("../../../img/splash.png"),
    sound: bodyCut,
  },
  {
    image: require("../../../img/lightning.png"),
    sound: thunder,
  },
  {
    image: require("../../../img/skull.png"),
    sound: devilLaugh,
  },
  {
    image: require("../../../img/warewolf.png"),
    sound: wolf,
  },
  {
    image: require("../../../img/zombie.png"),
    sound: zombie,
  },
  {
    image: require("../../../img/stomp.png"),
    sound: stomp,
  },
  {
    image: require("../../../img/horror.png"),
    sound: horror,
  },
  {
    image: require("../../../img/drum.png"),
    sound: drum,
  },
  {
    image: require("../../../img/sweep.png"),
    sound: sweep,
  },
  {
    image: require("../../../img/jigsaw.png"),
    sound: jigsaw,
  },
];

function Scare({ end, index }) {
  const { image, sound } = effects[index];
  return (
    <Container image={image} onAnimationEnd={end}>
      <iframe
        src={"../../../sounds/silence.mp3"}
        allow="autoplay"
        style={{ display: "none" }}
        id="iframeAudio"
      ></iframe>
      <audio autoPlay>
        <source src={sound} type="audio/wav" />
      </audio>
    </Container>
  );
}

export default Scare;
