import { Component } from "react";
import {
  Container,
  Riddle,
  Title,
  Audio,
  Input,
  Button,
  Label,
  ButtonContainer,
  Bulb,
  Hint,
  Quote,
  Picture,
  Background,
  Wrong,
} from "./styled";
import answer from "../../img/answer.png";
import next from "../../img/next.png";
import first from "../../sounds/indiana.mp3";
import canYouGuess from "../../img/can_you_guess.png";
import yourGuess from "../../img/your_guess.png";
import indiana from "../../img/indiana_quote.png";
import indy from "../../img/indy.png";
import indyBg from "../../img/indyBg.jpg";
import myHeart from "../../sounds/my_hear_will_go_on.mp3";
import titanicBg from "../../img/titanicBg.jpg";
import tit from "../../img/tit.png";
import titQ from "../../img/titQ.png";
import swbg from "../../img/swbg.webp";
import swq from "../../img/swq.png";
import swp from "../../img/swp.png";
import im from "../../sounds/im.mp3";
import mnbg from "../../img/mnbg.jpg";
import mnp from "../../img/mnp.png";
import mnq from "../../img/mnq.png";
import mn from "../../sounds/mn.mp3";
import fbg from "../../img/fbg.jpg";
import fp from "../../img/fp.png";
import fq from "../../img/fq.png";
import fs from "../../sounds/fs.mp3";
import stbg from "../../img/stbg.jpg";
import stp from "../../img/stp.png";
import stq from "../../img/stq.png";
import sts from "../../sounds/sts.mp3";
import ztbg from "../../img/ztbg.jpg";
import ztp from "../../img/ztp.png";
import ztq from "../../img/ztq.png";
import zts from "../../sounds/zts.mp3";
import arlbg from "../../img/arlbg.jpg";
import arlp from "../../img/arlp.png";
import arlq from "../../img/arlq.png";
import arls from "../../sounds/arls.mp3";
import rhpsbg from "../../img/rhpsbg.jpg";
import rhpsp from "../../img/rhpsp.png";
import rhpsq from "../../img/rhpsq.png";
import rhpss from "../../sounds/rhpss.mp3";
import iobg from "../../img/iobg.jpg";
import iop from "../../img/iop.png";
import ioq from "../../img/ioq.png";
import ios from "../../sounds/ios.mp3";
import frbg from "../../img/frbg.jpg";
import frp from "../../img/frp.png";
import frq from "../../img/frq.png";
import frs from "../../sounds/frs.mp3";

const movies = [
  {
    answers: [
      "indianajones",
      "poszukiwaczezaginionejarki",
      "indianajonesiposzukiwaczezaginionejarki",
      "raidersofthelostark",
    ],
    sound: first,
    quote: indiana,
    background: indyBg,
    picture: indy,
  },
  {
    answers: ["titanic"],
    sound: myHeart,
    quote: titQ,
    background: titanicBg,
    picture: tit,
  },
  {
    answers: ["starwars", "gwiezdnewojny", "nowanadzieja"],
    sound: im,
    quote: swq,
    background: swbg,
    picture: swp,
  },
  {
    answers: ["moana", "vaiana", "vaiana:skarboceanu", "vaianaiskarboceanu"],
    sound: mn,
    quote: mnq,
    background: mnbg,
    picture: mnp,
  },
  {
    answers: ["friends", "przyjaciele"],
    sound: fs,
    quote: fq,
    background: fbg,
    picture: fp,
  },
  {
    answers: ["strangerthings"],
    sound: sts,
    quote: stq,
    background: stbg,
    picture: stp,
  },
  {
    answers: ["zootopia", "zwierzogród"],
    sound: zts,
    quote: ztq,
    background: ztbg,
    picture: ztp,
  },
  {
    answers: ["ariel", "arielka", "małasyrenka", "littlemermaid"],
    sound: arls,
    quote: arlq,
    background: arlbg,
    picture: arlp,
  },
  {
    answers: ["rockyhorrorpictureshow"],
    sound: rhpss,
    quote: rhpsq,
    background: rhpsbg,
    picture: rhpsp,
  },
  {
    answers: ["insideout", "wgłowiesięniemieści"],
    sound: ios,
    quote: ioq,
    background: iobg,
    picture: iop,
  },
  {
    answers: ["frozen", "krainalodu"],
    sound: frs,
    quote: frq,
    background: frbg,
    picture: frp,
  },
];

class GuessTheMovie extends Component {
  constructor(props) {
    super(props);

    this.state = {
      value: "",
      showHint: false,
      showQuote: false,
      showPicture: false,
      idx: 0,
      correct: false,
      wrong: false,
    };
  }

  handleChange = (e) => {
    this.setState({
      value: e.target.value,
    });
  };

  handleAnswer = () => {
    const { idx, value } = this.state;
    const val = value.toLowerCase().replace(/ /g, "");
    if (movies[idx].answers.includes(val)) {
      this.showHint();
      this.setState({
        correct: true,
      });
    } else {
      this.setState({
        wrong: true,
      });
    }
  };

  onAnimationEnd = () => {
    this.setState({
      wrong: false,
    });
  };

  next = () => {
    const { idx } = this.state;
    const audio = document.querySelector("#audioPlayer");
    this.setState({
      idx: idx + 1,
      value: "",
      correct: false,
    });
    this.hideHint();
    audio.pause();
    audio.load();
  };

  hideHint = () => {
    this.setState({
      showHint: false,
      showQuote: false,
      showPicture: false,
    });
  };

  showHint = () => {
    this.setState({
      showHint: true,
    });
    const delayQuote = () => {
      this.setState({
        showQuote: true,
      });
    };
    const delayPicture = () => {
      this.setState({
        showPicture: true,
      });
    };
    setTimeout(delayQuote, 1500);
    setTimeout(delayPicture, 2000);
  };

  render() {
    const {
      showHint,
      showPicture,
      showQuote,
      idx,
      correct,
      wrong,
    } = this.state;
    const { sound, quote, background, picture } = movies[idx];
    const label = correct ? next : answer;
    return (
      <Container>
        <Title src={canYouGuess} />
        <Riddle animate={wrong}>
          {wrong && <Wrong onAnimationEnd={this.onAnimationEnd} />}
          {showHint && (
            <Hint>
              <Background image={background} />
              {showQuote && <Quote src={quote} />}
              {showPicture && <Picture src={picture} />}
            </Hint>
          )}
          <Bulb onClick={this.showHint} />
          <Audio controls id="audioPlayer">
            <source src={sound} type="audio/wav" />
          </Audio>
          <Label src={yourGuess} />
          <Input
            type="text"
            value={this.state.value}
            onChange={this.handleChange}
          />

          <ButtonContainer bg={label}>
            {!correct && <Button onClick={this.handleAnswer} />}
            {correct && <Button onClick={this.next} />}
          </ButtonContainer>
        </Riddle>
      </Container>
    );
  }
}

export default GuessTheMovie;
