import { Wrapper, Container, Image, Input, Button, Frame } from "./styled";
import secret from "../../../img/secret_place.jpg";

function Popup({ value, handleChange, check, animate, onAnimationEnd }) {
  return (
    <Wrapper>
      <Container>
        <Frame onAnimationEnd={onAnimationEnd} animate={animate} />
        <Image src={secret} />
        <Input type="text" autoFocus value={value} onChange={handleChange} />
        <Button onClick={check} />
      </Container>
    </Wrapper>
  );
}

export default Popup;
