import styled, { keyframes } from "styled-components";
import splatter from "../../img/splatter.png";
import check from "../../img/check_answers.png";
import tryAgain from "../../img/try_again.png";

export const Container = styled.div`
  height: 100vh;
  position: relative;
  padding: 100px 50px;

  &:before {
    content: "";
    top: 0;
    position: absolute;
    left: 0;
    background-color: black;
    z-index: -2;
    height: 100%;
    width: 100%;
  }

  &:after {
    content: "";
    opacity: 0.4;
    background-image: url(${splatter});
    background-size: cover;
    background-repeat: no-repeat;
    top: 0;
    position: absolute;
    left: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
  }
`;

export const Wrapper = styled.div`
  margin: auto;
`;

export const Boxes = styled.div`
  display: flex;
  flex-wrap: wrap;
  max-width: 1400px;
  margin: auto;
`;

export const Box = styled.div`
  width: 20%;
  padding: 10px;
  border: 1px solid red;
  display: flex;
`;

export const Season = styled.img`
  width: 45%;
  height: auto;
  margin-right: auto;
`;

export const DragTo = styled.img`
  background-color: rgba(255, 255, 255, 0.15);
  width: 50%;
  height: 175px;
`;

export const Img = styled.img`
  max-width: 100px;
  height: auto;
  margin: 10px;
`;

export const Evans = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: auto;
`;

export const Evan = styled.img`
  height: 175px;
  width: auto;
  margin: 30px auto;
`;

export const Button = styled.button`
  background-image: url(${check});
  border-radius: 15px;
  background-color: black;
  transition: all 0.25s ease-out;
  width: 500px;
  height: 75px;
  background-size: 350px;
  background-repeat: no-repeat;
  background-position: center;
  padding: 10px;
  margin-bottom: 50px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }
`;

const blink = keyframes`
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
`;

export const TryAgain = styled.div`
  opacity: 0;
  width: 500px;
  height: 100px;
  background-image: url(${tryAgain});
  background-position: center 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  background-color: black;
  padding: 50px;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  animation: ${blink} 2s linear;
`;
